<template>
  <div class="app-container">
    <van-nav-bar
      class="bar"
      fixed
      placeholder
      title="报告列表"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="list-box">
        <div class="list-item flex-wrap" v-for="(item,index) in list" :key="index" @click="handelReport(item.id)">
           <div> {{item.realName}}</div>
           <div> {{item.createTime}}</div>
           <van-icon name="arrow" />
        </div>
    </div>
     <van-overlay :show="show" @click="show = false" >
      <div class="wrapper">
        <van-loading  class="block" size="50px" vertical type="spinner"> 加载中...</van-loading>
      </div>
    </van-overlay>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
import {medicalRecordPatientList} from '@/api/medicalRecord'
export default {
  name: "history",
  data() {
    return {
      show:false,
        list:[]
    };
  },
  computed:{
      ...mapGetters([             //步骤二，对象扩展运算符方式
        "info"
      ])
    },
  mounted() {
     
     this.getMedicalRecordPatientList()
  },
  methods: {
     
    getMedicalRecordPatientList(){
      let  listQuery= {
          examinationId: "",
          examinationStatus: "100",
          patientId: this.info.id,
          pageNum: 1,
          pageSize: 10
        }
        this.show=true;
      medicalRecordPatientList(listQuery).then(res=>{
        this.show=false;
                if(res.code==200){
                    this.list=res.dataList;
                }
            })
    },
    handelReport(val){
       this.$router.push({path:"/home",query:{id:val}})
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
    
<style lang="less" scoped>
.app-container {
  height: 100vh;
  background: #f5f5f5;
  /deep/.van-nav-bar {
    background-color: #1092d4;
    color: #fff;
    .van-icon {
      color: #fff;
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  /deep/ .van-nav-bar__text {
    color: #fff;
  }
}
.list-box{

    .list-item{
        margin-top:5px;
        padding:  20px;
        background:#fff;
        border: 1px solid #eee;
        font-size: 14px;
        color: #666;
    }
}
</style>


